<template>
	<div class="rights02_wrap">
		<el-row :gutter="10" class="rights02-top">
			<el-col :span = "5">
				<el-input  placeholder="请输入BO_ID 或 BoName" prefix-icon="el-icon-search" v-model="searchData.input1" @change="inpChange"></el-input>
			</el-col>
			
			<!--<el-col :span = "5">
				<el-date-picker
			      	type="daterange"
			      	v-model="searchData.startDate_endData"
			      	start-placeholder="开始时间"
	      		  	end-placeholder="结束时间"
	      		  	value-format="yyyy-MM-dd"
	      		  	style="width:250px">
			    </el-date-picker>
			</el-col>-->
			<!--<el-col :span= "5">
				<el-select v-model="searchData.role" placeholder="请选择角色" style="width: 100%;">
				    <el-option
				        v-for="item in options"
				        :key="item.value"
				        :label="item.label"
				        :value="item.value">
				    </el-option>
			    </el-select>
			</el-col>-->
			<el-col :span="5" >
				<el-button icon="el-icon-search" @click="searchFun"></el-button>
				<!--<el-button type = "primary">批量导出</el-button>-->
			</el-col>
		</el-row>
	
		<div  style="margin-top:10px;padding: 20px 30px;background: #FFFFFF;">
			<el-row style="padding-bottom: 20px; ">
				<el-col :span="4" :offset="20">
					<router-link :to="{path:'/creatRights_list'}">
						<el-button type="success">新增权限对象</el-button>
					</router-link>
					<!--<el-button > 批量删除</el-button>-->
				</el-col>
		
			</el-row>
			<el-row>
				<el-col>
					<el-table  :data="rightsData"  style="width: 100%" border>
						<!--<el-table-column  prop="BO_ID" label="Bo_ID">
				        </el-table-column>-->
				        <el-table-column prop="BO_NAME"  label="BoName" >
				        </el-table-column>
				        <el-table-column prop="BO_KEY" label="Bokey">
				        </el-table-column>
				        <el-table-column  prop="BO_DESC" label="BoDESC">
				        </el-table-column>
				        <el-table-column  prop="BO_VALUE" label="Bovalue">
				        </el-table-column>
				        <el-table-column  prop="BPLATFORM" label="系统">
				        </el-table-column>
				        <el-table-column   label="操作">
				        	<template slot-scope="scope">
				        		<el-button type="info" icon="el-icon-edit" size="mini" @click ="editFun({id:scope.row.DB_ID})"></el-button>
				        		<el-button type="danger" icon="el-icon-delete" size="mini" @click = "delFun({id:scope.row.DB_ID})"></el-button>
				        	</template>
				        </el-table-column>
				    </el-table>
				</el-col>
				<!--删除弹框-->
				<el-dialog
				    :visible.sync="oneDeldialogVisible"
				    width="30%"
				    :before-close="handleClose"
				    >
				    <div style="text-align: center;">
				    	<img src="../../assets/del-image/del_image.png"/>
				    	<h3	>删除操作</h3>
				    	<span>删除后不可恢复，确认要删除吗？</span>
				    </div>
				    <span slot="footer" class="dialog-footer dialog-footer_span">
					    <el-button @click="oneDeldialogVisible = false">我在想想</el-button>
					    <el-button type="primary" @click="confirmDelete">确定</el-button>
				    </span>
				</el-dialog>
			</el-row>
			<el-row style="padding-top:20px ;">
				<el-col :span="4" :offset="14">
					<el-pagination 
						@size-change="handleSizeChange"
						@current-change="handleCurrentChange"
			            :current-page="currentPage"
			            :total="organNumber"
			            :page-size="pagesize"		        		
			            layout="total, prev, pager, next, jumper">
					</el-pagination>
				</el-col>
			</el-row>		
		</div>
	</div>	
</template>

<script>
	import {
		queryBoPort ,   //查询列表
		deleteBoByDBIdPort   //删除权限对象
	}from 'api/rightsObject.js'
	export default{
		data(){
			return {
				oneDeldialogVisible:false,   //删除弹框
				delId:'',   //删除id
				//筛选
		  		searchData:{
		  			input1:'',
//		  			startDate_endData:'',
//		  			role:''
		  		},
		  		options:[],
		        rightsData:[],
		        pagesize:5,
		   		currentPage:1,
		   		organNumber:0
			}
		},
		created(){
			this.initRights()
		},
		methods:{
			//table列表
			initRights(){
				let resData = {
					limit:this.currentPage,    //当前页
	  				offset:this.pagesize ,//条数
	  				BOIDORBONAME:this.searchData.input1
				}
				queryBoPort(resData).then( res =>{
					this.organNumber = res.TOTALRESULT
					this.rightsData = res.ALLBO
					console.log('table列表'+JSON.stringify(res))
				})
			},
			//搜索筛选
			searchFun(){
				this.initRights()
			},
			//输入变动时间
			inpChange(){
				if(this.searchData.input1 ==''){
					this.initRights()
				}
			},
			//编辑按钮
			editFun(obj){
				this.$router.push({
					name:'editRights_list',
					params:{id:obj.id}
				})
			},
			//删除按钮
			delFun(obj){
				this.delId = obj.id
				this.oneDeldialogVisible = true
			},
			//确定删除
			confirmDelete(){
				this.oneDeldialogVisible = false
				deleteBoByDBIdPort({DB_ID:this.delId}).then( res =>{
					this.initRights()
//					console.log('删除权限对象'+JSON.stringify(res))
				})
			},
			 //初始页currentPage、初始每页数据数pagesize和数据data
	    	handleSizeChange: function (size) { 
				this.pagesize = size; 
			}, 
			handleCurrentChange: function(currentPage){ 
				this.currentPage = currentPage; 
				this.initRights()
			},
			handleClose(done,event) {
	            this.$confirm('确认关闭？')
	            .then(() => {
	           	 	done();
	            })
	            .catch(() => {});
	        },

		}
	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	$border:1px solid #eee;
	.rights02_wrap{
		.rights02-top{
			background: #fff;
			padding: 20px;
			border-radius: 6px;
		}
	}
</style>



